<script>
export default {
  name: "Register",
  data() {
    return {
      role_list: [] ,
      contact: {
        name: "",
        phone: "",
        email: "",
        password: "",
        reason: "",
        selected_role: this.params.job,
      },
      file: null,
      error: ""
    };
  },
  mounted() {
      this.axios.get(process.env.VUE_APP_API_URL + "/api/rolee_published")
      .then(response => {
          this.role_list = response.data;
      })
      .catch(err => console.log(err));
  },
  methods: {
    submit(event) {
      const data = new FormData();
      data.append('name', this.contact.name);
      data.append('email', this.contact.email);
      data.append('phone_number', this.contact.phone);
      data.append('password', this.contact.password);
      data.append('cv', this.file);
      this.contact.selected_role.forEach(role => {
        data.append('freelance_role_id[]', role);
      });
      // data.append('freelance_role_id[]', this.contact.selected_role);
      data.append('aboutsirkel', this.contact.reason);
      event.preventDefault();
      var submit_button = document.getElementById("submit-button");
      submit_button.innerHTML =
        '<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>Submitting...';
      this.axios
        .post(process.env.VUE_APP_API_URL + "/api/v1/register_freelancer", 
        data, 
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          if (response.data == "Record has been created") {
            submit_button.innerHTML = "SUBMIT";
            this.showSuccess();
          } else {
            submit_button.innerHTML = "SUBMIT";
            this.showError(response.data);
          }
        })
        .catch(error => {
          let modalMessage = "";
          for (let message in error.response.data.errors) {
            modalMessage += error.response.data.errors[message][0] + "<br>";
          }
          submit_button.innerHTML = "SUBMIT";
          this.showError(modalMessage);
        });
    },
    showModal() {
      this.$bvModal.show("modal-clear-form");
    },
    closeModal() {
      this.$bvModal.hide("modal-clear-form");
    },
    showError(message) {
      this.error = message;
      this.$bvModal.show("modal-failed");
    },
    showSuccess(){
      this.error = "We will notify you further via Email. ASAP!";
      this.$bvModal.show("modal-success");
    },
    closeSuccess(){
      this.$bvModal.hide("modal-success");
      this.$router.push('/');
    },
    closeError() {
      this.$bvModal.hide("modal-failed");
    },
  },
  props: {
        params: {
            type: Object,
            default: function () {
                return {
                    role : [],
                    }
            }
        }
    }
};
</script>

